@import '../../assets/css/variable.less';

.dip-box {
  border-top: 1px solid @divider_color ;
  font-weight: 500;

  .back-governance {
    border-top: 1px solid #203b60;
    display: flex;
    font-size: 24px;

    img {
      width: 16px;
      height: 16px;
      margin-right: 16px;
    }

    .icon-back {
      height: 48px;
      display: flex;
      align-items: center;
      position: absolute;
      padding-left: 123px;
      padding-top: 120px;
    }

    .poll-detail {
      font-weight: 600;
      font-size: 32px;
      color: #FFF;
      margin: 0 auto;
      margin-top: 120px;
    }

  }

  .dip-box-info {
    width: 760px;
    margin: 0 auto;
    margin-top: 72px;

    .poll-dip-info {
      height: 182px;
      background: #112247;
      border-radius: 8px;
      padding: 22px 30px;

      .dip-list-title {
        color: #93A1B8;
        font-size: 14px;
        display: flex;
        justify-content: space-between;

        .dip-executed {
          display: flex;
          align-items: center;

          .executed-bg {
            width: 12px;
            height: 12px;
            background-color: #CD7A37;
            border-radius: 50%;
            margin-right: 8px;
          }
        }
      }

      .dip-list-des {
        margin-top: 32px;
        min-width: 449px;
        height: 36px;
        display: flex;
        align-items: center;
        font-size: 18px;
        color: #E0ECFF;
      }

      .dip-time {
        font-size: 12px;
        color: #52617F;
        margin-top: 49px;
      }
    }

    .dip-option {
      margin-top: 33px;
      height: 430px;
      background: #112247;
      border-radius: 8px;
      padding: 22px 30px;
      display: flex;
      flex-direction: column;
      align-items: center;

      .option-check {
        width: 600px;
        height: 100%;
        padding-top: 12px;
        padding-left: 8px;
        padding-right: 21px;
        border: 1px solid transparent;
        border-radius: 8px;
        margin-top: 20px;
        cursor: pointer;

        .status-quo {
          width: 100px;
          height: 17px;
          border-radius: 16px;
          border: 1px solid #FFF;
          display: flex;
          justify-content: center;
          align-items: center;
          font-size: 10px;
          margin-bottom: 10px;
        }

        .leading {
          width: 100px;
          height: 17px;
          color: #59AE99;
          border-radius: 16px;
          border: 1px solid #59AE99;
          display: flex;
          justify-content: center;
          align-items: center;
          font-size: 10px;
          margin-bottom: 10px;
        }

        .option-title {
          display: flex;
          align-items: center;
          justify-content: space-between;
          height: 18px;

          .option-check-text {
            display: flex;
          }

          .option-title-text {
            font-size: 18px;
          }

          .option-num {
            font-size: 14px;
          }

          input[type=radio] {
            position: absolute;
            clip: rect(0, 0, 0, 0);
          }

          label {
            font-size: 32px;
            font-weight: 600;
            color: #e0ecff;
            display: flex;
            align-items: center;
            cursor: pointer;
          }

          input[type=radio]+label:before {
            content: "";
            display: inline-block;
            width: 13px;
            height: 13px;
            border-radius: 50%;
            border: 1px solid #fff;
            margin-right: 13px;
          }

          input[type=radio]:checked+label:before {
            background-clip: content-box;
            background-color: #fff;
            width: 8px;
            height: 8px;
            padding: 4px;
            border: 1px solid #fff;
          }
        }

        .option-peng-bg {
          margin-top: 8px;
          width: 0;
          height: 16px;
          border-radius: 8px;
          margin-bottom: 10px;
          background: radial-gradient(29.9% 70.94% at 44.25% 86.96%, rgba(89, 174, 153, 0.2) 0%, rgba(220, 141, 220, 0) 100%), radial-gradient(33.83% 53.57% at 35.87% 100%, rgba(89, 174, 153, 0.2) 0%, rgba(220, 141, 220, 0) 100%), radial-gradient(38.83% 66.43% at 51.21% 43.26%, rgba(89, 174, 153, 0.2) 0%, rgba(194, 166, 241, 0) 100%), linear-gradient(134.59deg, rgba(85, 119, 253, 0.5) 20.63%, rgba(205, 249, 232, 0) 47.84%), linear-gradient(220.99deg, rgba(85, 119, 253, 0.5) -15.86%, rgba(89, 174, 153, 0.5) 10.77%, rgba(89, 174, 153, 0.5) 29.79%), linear-gradient(128.53deg, rgba(85, 119, 253, 0.5) 28.63%, rgba(89, 174, 153, 0.5) 38.5%, rgba(89, 174, 153, 0.5) 50.26%), #5577FD;
        }

        .option-one-bg {
          width: 31%;
        }

        .option-two-bg {
          width: 69%;
        }
      }

      .option-check:hover {
        border: 1px solid #E27625;

        .option-title-texte {
          color: #FFF;
        }
      }

      .vote-button {
        margin-top: 24px;
      }
    }

    .dip-option-des-rules {
      margin-top: 33px;
      margin-bottom: 24px;
      min-height: 600px;
      background: #112247;
      border-radius: 8px;
      padding: 22px 30px;

      .description-text {
        color: #FFF;
        margin-top: 16px;

        .ul-description {
          ul {
            padding-left: 24px;

            li {
              margin-top: 24px;
            }
          }

        }
      }

      .supplementary-notes {
        margin-top: 32px;

        .notes-text {
          margin-top: 16px;
          color: #FFF;

          a {
            color: #cd7a37;
          }
        }
      }

      .vote-rules {
        margin-top: 32px;

        .rules-text {
          color: #FFF;

          .rules-margin {
            margin-top: 16px;
          }

          ul {
            padding-left: 24px;

            li {
              margin-top: 24px;
            }
          }

          .no-margin {
            margin: 0;

            li {
              margin-top: 0;
            }
          }
        }
      }

    }
  }
}

@media only screen and (max-width : 770px) {
  .dip-box {
    .back-governance {
      .icon-back {
        padding-top: 16px;
        padding-left: 24px;
      }
      .poll-detail{
        margin-top: 48px;
      }
    }

    .dip-box-info {
      width: 375px;
      .poll-dip-info {
        padding: 24px 16px;

        .dip-list-des {
          width: 327px;
          min-width: 327px;
        }
      }
      .dip-one-option{
        height: 500px;
      }
      .dip-option {
        .option-check {
          width: 327px;
          .dip-one-option-title{
           height: 45px;
          }
        }
      }
    }
  }
}